@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  background-color: #111;
}

body.is-open {
  overflow: hidden;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.button__link {
  position: relative;
  overflow: hidden;
  text-align: center;
  display: inline-block;
  font-size: 1.25rem;
  font-weight: 400;
  width: 20.9375rem;
  text-transform: uppercase;
  border: 1px solid #fff;
  background: #333;
  line-height: 1;
  padding: 0.875rem 0;
  letter-spacing: 0.1em;
  transition: ease 0.3s;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .button__link {
    font-size: 1rem;
    line-height: 1.2;
    width: 22.5rem;
    padding: 1.1875rem 0;
  }
}

.button__link span {
  position: relative;
  z-index: 3;
  color: #fff;
}

.button__link:hover span {
  color: #333;
}

.button__animation:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background: #fff;
  width: 100%;
  height: 100%;
  transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1) 0s;
  transform: scale(0, 1);
  transform-origin: right top;
}

.button__animation:hover:before {
  transform-origin: left top;
  transform: scale(1, 1);
}

.card {
  position: relative;
  display: block;
  background-color: #fff;
  max-width: 37.5rem;
  margin: 0 auto;
}

.card__update {
  position: absolute;
  top: -0.9375rem;
  left: -0.9375rem;
  background-color: #FFBA6A;
  border-radius: 3.125rem;
  font-size: 1rem;
  color: #333;
  letter-spacing: 0.1em;
  width: 3.75rem;
  height: 3.75rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .card__update {
    top: -6%;
    left: -4%;
  }
}

.card__image {
  aspect-ratio: 350/233;
}

.card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card__body {
  padding: 0.25rem 1.5rem 3.0625rem;
}

.card__body:hover {
  background-color: #333;
}

.card:hover :not(.card__category):not(.card__update):not(.card__update > p) {
  background-color: #333;
  transition: 0.3s;
  color: #fff;
}

.card__title,
.card__text,
.card__date {
  color: #333;
}

.card__title {
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 2;
  letter-spacing: 0.05em;
}

.card__text,
.card__category,
.card__date {
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.85714;
  letter-spacing: 0.06em;
}

.card__info {
  padding: 0 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card__category {
  color: #111;
  background-color: #FFEA2E;
  padding: 0.375rem 0.3125rem;
  line-height: 1;
  position: absolute;
  bottom: 0.9375rem;
  left: 1.5rem;
}

.card__date {
  position: absolute;
  bottom: 0.9375rem;
  right: 1.5rem;
}

.card__text {
  padding-top: 0.25rem;
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5625rem;
}
@media screen and (min-width: 768px) {
  .cards {
    grid-template-columns: repeat(3, 1fr);
  }
}

