  bottom: -0.3125rem;
  left: 10%;
  width: 80%;
  height: 0.0625rem;
  background: #FFEA2E;
  transition: all 0.3s;
  transform: scale(0, 1);
  transform-origin: center top;
}

.footer-nav__item.current a::after,
.footer-nav__item a:hover::after {
  transform: scale(1, 1);
}

.footer {
