    margin: 0;
  }
}

.footer__copyright {
  text-align: center;
  margin-top: 2rem;
  color: #fff;
  font-size: 0.6875rem;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.10909091em;
}
@media screen and (min-width: 768px) {
  .footer__copyright {
    margin-top: 5.1875rem;
    font-size: 0.9375rem;
    letter-spacing: 0.18em;
  }
}

#footer__page-top a {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  width: 2.8125rem;
  height: 2.8125rem;
  color: #fff;
  text-align: center;
  text-decoration: none;
  transition: all 0.3s;
}

#footer__page-top a:hover {
  background: #333;
}

#footer__page-top {
  position: fixed;
  right: 1.25rem;
  bottom: 3.25rem;
  z-index: 2;
  opacity: 0;
  transform: translateY(100px);
}
@media screen and (min-width: 768px) {
  #footer__page-top {
    right: 1.9375rem;
    bottom: 2rem;
  }
}

#footer__page-top.UpMove {
  animation: UpAnime 0.5s forwards;
}

@keyframes UpAnime {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
#footer__page-top.DownMove {
  animation: DownAnime 0.5s forwards;
}

@keyframes DownAnime {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 1;
    transform: translateY(100px);
  }
}
.footer__page--arrow {
  height: 1.25rem;
  width: 1.25rem;
  border-top: 0.125rem solid #333;
  border-right: 0.125rem solid #333;
  transform: translateY(20%) rotate(-45deg);
}

#footer__page-top a:hover .footer__page--arrow {
  border-top: 0.125rem solid #fff;
  border-right: 0.125rem solid #fff;
}

.hamburger {
  z-index: 9999;
}
@media screen and (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.hamburger span {
  position: relative;
  display: block;
  height: 0.125rem;
  width: 1.5rem;
  background: #fff;
  transition: ease 0.3s;
}

.hamburger span:nth-child(1) {
  top: 0;
}

.hamburger span:nth-child(2) {
  margin: 0.25rem 0;
}

.hamburger span:nth-child(3) {
  top: 0;
}

/*is-OPEN時の動き*/
.hamburger.is-open span:nth-child(1) {
  top: 0.3125rem;
  transform: rotate(45deg);
}

.hamburger.is-open span:nth-child(2) {
  transform: translateY(-50%);
  opacity: 0;
}

.hamburger.is-open span:nth-child(3) {
  top: -0.4375rem;
  transform: rotate(-45deg);
}

