@use 'global'as *;

.media {
  @include mq(md) {
    display: flex;
    gap: 3.1%;
    align-items: flex-start;
  }
}

.media--reverse {
  @include mq(md) {
    flex-direction: row-reverse;
  }
}

.media__images {
  aspect-ratio: 375/261;
  width: 100%;
  width: 100vw;
  margin-left: calc(50% - 50vw);

  @include mq(md) {
    aspect-ratio: 557/389;
    width: 50.6%;
    margin-left: unset;
  }
}

.media__images img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  max-width: rem(600);
  margin-right: auto;
  margin-left: auto;
}

.media__textArea {
  padding: rem(30) rem(20) rem(15);
  width: 100%;
  color: $white;
  width: 100vw;
  margin-left: calc(50% - 50vw);
  text-align: center;

  @include mq(md) {
    padding: 0;
    padding-top: rem(35);
    width: 46.3%;
    margin: 0 auto;
    text-align: left;
  }
}

.media__mainTitle {
  font-size: rem(20);
  font-weight: $regular;
  line-height: 1;
  letter-spacing: 0.1em;
  text-indent: 1em;

  @include mq(md) {
    font-size: rem(25);
    letter-spacing: 0.02em;
    text-indent: 0;
  }
}

.media__text {
  margin-top: rem(27);
  font-size: rem(16);
  text-align: left;
  line-height: 1.45;
  letter-spacing: 0.12em;

  @include mq(md) {
    line-height: 2.19;
    margin-top: rem(43);
    letter-spacing: 0.03em;
  }
}

.media__button {
  margin-top: rem(27);

  @include mq(md) {
    margin-top: rem(48);
  }
}