@use 'global'as *;

.smallButton__link {
  position: relative;
	overflow: hidden;
  font-size: rem(16);
  font-weight: $medium;
  color: #000;
  width: rem(136);
  background: $white;
  padding: rem(5) rem(27);
  white-space: nowrap;
  letter-spacing: 0.04em;
  @include mq(md){
  }
}

.smallButton__link span {
  position: relative;
  z-index: 3;
  color: $white;
  @include mq(md){
  color: #000;
}
}

.smallButton__link:hover {
  border: rem(1) solid $black2;
}

.smallButton__link:hover span {
  color: $white;
}

.smallButton__link:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background:$black2;
  width: 100%;
  height: 100%;
  transition: transform .6s cubic-bezier(0.8, 0, 0.2, 1) 0s;
    transform: scale(0, 1);
	transform-origin: right top;
}

.smallButton__link:hover:before{
	transform-origin:left top;
	transform:scale(1, 1);
}